<template>
  <div class="love-oath">
    <div class="banner">
      <div id="scene">
        <div data-depth="0.00" class="layer"></div>
        <div data-depth="0.88" class="stars-cluster stars-cluster-1 layer">
          <img src="../../static/img/banner_icon03.png" class="banner-icon03">
        </div>
        <div data-depth="0.99" class="moon-wrapper layer">
          <img src="../../static/img/banner_icon02.png" class="banner-icon02">
        </div>
        <div data-depth="0.66" class="title-wrapper layer">
          <img src="../../static/img/banner_icon01.png" class="banner-icon01">
        </div>
        <div data-depth="0.77" class="title-wrapper layer">
          <div class="btn">
            <router-link to="/index"  class="btn-left">爱情誓言</router-link>
            <router-link to="/myLove"  class="btn-right">我的宣言</router-link>
          </div>
        </div>
      </div>
    </div>
    <!--/banner-->

    <div class="row my-oath issue-an-oath">
      <h2>发布誓言</h2>
      <form @submit="onSubmit" @reset="onReset">
        <div class="thumbnail">
          <div class="con4">
            <canvas id="cvs" width="220" height="220"></canvas>
            <span class="btn upload">上传头像<input type="file" class="upload_pic" id="upload" v-on:change="getFile"></span>
          </div>
        </div>
        <div class="caption">
          <div class="form-group">
            <label for="exampleInputEmail1">爱情宣言主题*</label>
            <input type="text" class="form-control" v-model="form.title">
          </div>
          <div class="form-group">
            <label for="exampleInputEmail1">爱情宣言内容*</label>
            <textarea class="form-control" rows="3" v-model="form.message"></textarea>
          </div>
          <div class="form-group">
            <button type="reset" class="btn">重置</button>
            <button type="submit" class="btn btn-default">发布誓言</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script >
import api from '../libs/api'
import image from '../libs/image'

export default {
  name: 'CreateLove',
  data () {
    return {
      form: {
        title: '',
        message: '',
        file: ''
      }
    }
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      image.PhotoCompress(this.form.file, {
        quality: 0.8,
        width: 640
      }, re => {
        api.publishLove(this.form.title, this.form.message, re, resp => {
        })
      })
    },
    onReset (evt) {
      evt.preventDefault()
      this.form.title = ''
      this.form.message = ''
      this.form.file = ''
    },
    getFile($event){
      this.form.file = $event.target.files[0] //获取要上传的文件
      if(!/image\/\w+/.test(this.form.file.type)){
        //图片文件的type值为image/png或image/jpg
        alert("文件必须为图片！");
        return false;
      }
      // console.log(file);
      var reader = new FileReader();//实例一个文件对象
      reader.readAsDataURL(this.form.file);//把上传的文件转换成url
      //当文件读取成功便可以调取上传的接口
      reader.onload = function(e){
        // console.log(this.result);//文件路径
        // console.log(e.target.result);//文件路径
        /*var data = e.target.result.split(',');
            var tp = (file.type == 'image/png')? 'png': 'jpg';
            var imgUrl = data[1];//图片的url，去掉(data:image/png;base64,)
            //需要上传到服务器的在这里可以进行ajax请求
            // console.log(imgUrl);
            // 创建一个 Image 对象
            var image = new Image();
            // 创建一个 Image 对象
            // image.src = imgUrl;
            image.src = e.target.result;
            image.onload = function(){
                document.body.appendChild(image);
            }*/

        var image = new Image();
        // 设置src属性
        image.src = e.target.result;
        var max=200;
        // 绑定load事件处理器，加载完成后执行，避免同步问题
        image.onload = function(){
          // 获取 canvas DOM 对象
          var canvas = document.getElementById("cvs");
          // 如果高度超标 宽度等比例缩放 *=
          /*if(image.height > max) {
                    image.width *= max / image.height;
                    image.height = max;
                } */
          // 获取 canvas的 2d 环境对象,
          var ctx = canvas.getContext("2d");
          // canvas清屏
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          // 重置canvas宽高
          /*canvas.width = image.width;
                canvas.height = image.height;
                if (canvas.width>max) {canvas.width = max;}*/
          // 将图像绘制到canvas上
          // ctx.drawImage(image, 0, 0, image.width, image.height);
          ctx.drawImage(image, 0, 0, 220, 220);
          // 注意，此时image没有加入到dom之中
        };
      }
    }
  }
}
</script>



